html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  color: #53575a;
  line-height: 1.6; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700; }

a {
  text-decoration: none; }

img {
  width: 100%;
  display: block;
  max-width: 100%; }

.l-container {
  max-width: 1400px;
  margin: 0 auto; }

.nav .menu {
  display: flex; }
  .nav .menu__item {
    padding: 0 24px 0 0;
    display: flex;
    align-items: center;
    height: 24px; }
  .nav .menu__link {
    font-size: 15px;
    color: #53575a;
    height: 50px;
    display: flex;
    align-items: center;
    padding: 0 12px;
    position: relative; }
    .nav .menu__link--active {
      color: #ff1f5a;
      border-bottom: 3px solid #ff1f5a; }

.button {
  border: none;
  background-color: #ff1f5a;
  color: #ffffff;
  font-family: "Lato", sans-serif;
  padding: 12px;
  font-size: 16px;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s cubic-bezier(0.54, 0.11, 0.24, 1.3);
  border-radius: 3px;
  text-shadow: 0 0.5px 1px #141618;
  text-transform: uppercase; }
  .button:hover {
    background-color: #eb003e; }
  .button--primary {
    background: #ff1f5a; }
    .button--primary:hover {
      background-color: #eb003e; }
  .button--secondary {
    background: #ffd615; }
    .button--secondary:hover {
      background-color: #e1ba00; }
  .button--terciary {
    background: #f9ff21; }
    .button--terciary:hover {
      background-color: #e7ed00; }
  .button--like {
    background: transparent;
    width: 16px;
    padding: 0; }
    .button--like:hover {
      background-color: rgba(0, 0, 0, 0); }

.aside {
  position: relative;
  display: flex;
  align-items: center; }
  .aside__profile {
    position: relative; }
    .aside__profile-image {
      width: 40px;
      border-radius: 50%;
      margin-left: 8px;
      cursor: pointer; }
    .aside__profile-menu {
      width: 200px;
      min-height: 50px;
      position: absolute;
      top: calc(100% + 10px);
      right: 0;
      padding: 16px;
      border: 1px solid #dfe0e0;
      border-radius: 5px;
      box-shadow: 1px 1px 5px #dfe0e0;
      z-index: 2;
      background-color: #ffffff;
      display: flex;
      flex-direction: column; }
    .aside__profile-username {
      border-bottom: 1px solid #dfe0e0;
      margin: 0;
      padding: 4px 0;
      margin-bottom: 16px; }

.header {
  border-bottom: 1px solid #dfe0e0; }
  .header__container {
    display: flex;
    height: 50px;
    justify-content: space-between;
    align-items: center; }

.dashboard {
  display: flex;
  flex-direction: column;
  position: relative; }
  .dashboard-profile {
    display: flex;
    justify-content: center;
    text-align: center; }

.login {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden; }
  @media screen and (min-width: 640px) {
    .login {
      display: grid;
      grid-template-columns: 3fr 1fr; } }
  .login__tilt {
    width: 100%;
    max-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 10px solid #ff1f5a; }
    @media screen and (min-width: 640px) {
      .login__tilt {
        border-bottom: none;
        border-right: 10px solid #ff1f5a; } }
  .login__image {
    object-fit: cover;
    height: 50vh; }
    @media screen and (min-width: 640px) {
      .login__image {
        height: 100vh; } }
  .login__button {
    margin: auto;
    display: block; }
    @media screen and (min-width: 640px) {
      .login__button {
        max-height: 50vh;
        justify-self: center;
        align-self: center; } }
  .login-aside {
    height: 100vh; }
  .login-form {
    align-self: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 220px; }
    .login-form h3 {
      font-size: 28px; }
    .login-form input {
      padding: 8px;
      margin-bottom: 16px;
      border-radius: 5px;
      border: 1px solid #dfe0e0;
      width: 250px; }
  .login-comments {
    margin-top: 32px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-self: flex-end; }
    .login-comments h3 {
      font-size: 28px; }

.reset-form {
  align-self: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 8rem; }
  .reset-form h3 {
    font-size: 28px; }
  .reset-form input {
    padding: 8px;
    margin-bottom: 16px;
    border-radius: 5px;
    border: 1px solid #dfe0e0;
    width: 250px; }

.loader--full {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

.sk-cube-grid {
  width: 50px;
  height: 50px;
  margin: 100px auto; }

.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #ff1f5a;
  float: left;
  -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
  animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out; }

.sk-cube-grid .sk-cube1 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.sk-cube-grid .sk-cube2 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.sk-cube-grid .sk-cube3 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s; }

.sk-cube-grid .sk-cube4 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.sk-cube-grid .sk-cube5 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.sk-cube-grid .sk-cube6 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.sk-cube-grid .sk-cube7 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s; }

.sk-cube-grid .sk-cube8 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.sk-cube-grid .sk-cube9 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

@-webkit-keyframes sk-cubeGridScaleDelay {
  0%,
  70%,
  100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1); }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1); } }

@keyframes sk-cubeGridScaleDelay {
  0%,
  70%,
  100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1); }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1); } }

